<template>
  <view class="echarts-wrapper" :style="{height: height, width: width}">
    <ec-canvas 
      v-if="isCanvasReady"
      :ec="ec" 
      :canvasId="canvasId" 
      :forceUseOldCanvas="forceUseOldCanvas"
      @inited="handleInit"
    ></ec-canvas>
  </view>
</template>

<script>
import EcCanvas from '../ec-canvas/ec-canvas.vue';

export default {
  components: {
    EcCanvas
  },
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '300px'
    },
    canvasId: {
      type: String,
      default: function() {
        return `echarts-canvas-${Date.now()}`;
      }
    },
    option: {
      type: Object,
      default: () => ({})
    },
    forceUseOldCanvas: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isCanvasReady: false,
      ec: {
        instance: null,
        option: this.option
      }
    }
  },
  watch: {
    option: {
      handler(newVal) {
        this.updateChart(newVal);
      },
      deep: true
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.isCanvasReady = true;
    },
    
    handleInit(chartInstance) {
      this.ec.instance = chartInstance;
      this.$emit('inited', chartInstance);
      
      if (this.option) {
        this.updateChart(this.option);
      }
    },
    
    updateChart(option) {
      if (this.ec && this.ec.instance) {
        this.ec.instance.setOption(option, true);
      }
    },
    
    // 提供刷新图表的方法
    refresh() {
      if (this.ec && this.ec.instance) {
        this.ec.instance.resize();
      }
    },
    
    // 提供清除图表的方法
    clear() {
      if (this.ec && this.ec.instance) {
        this.ec.instance.clear();
      }
    },
    
    // 提供销毁图表的方法
    dispose() {
      if (this.ec && this.ec.instance) {
        this.ec.instance.dispose();
      }
    }
  }
}
</script>

<style scoped>
.echarts-wrapper {
  position: relative;
  width: 100%;
  height: 300px;
}
</style> 